.main {
  position: relative;
  width: 100%;
  height: fit-content;
  background-color: aliceblue;
  padding: 2em 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#title,
#desc {
  margin-top: 1em;
  width: 100%;
  border-radius: 5px;
  border: 1px solid #39a0e4;
  padding: 0.4em 0.4em;
  font-size: 1.25rem;
}
#desc {
  font-size: 1rem;
  color: #777;
}
#title:focus,
#desc:focus {
  outline: 3px solid #b4d1e5;
}
#input-container {
  width: 25rem;
}
#add-btn{
    margin-top: 1em;
}
@media only screen and (max-width: 500px){
  #input-container{
    width: 85%;
  }
}
