.todo-container{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40%;
    width: 35rem;
    /* width: fit-content; */
    /* max-width: 40%; */
    margin-top: 1em;
    font-size: 1rem;
    background-color: aliceblue;
    border: 1px solid black;
    border-radius: 7px;
    padding: 1em 0.1em;
}
#todo-text-container{
    text-align: left;
    padding: 0 0.25em;
    width: 80%;
    /* background-color: aliceblue; */
    /* padding: 1em; */
    /* border-radius: 7px; */
    /* border: 1px solid black; */
}
.todo-title{
    font-size: 1.25rem;
    font-weight: 500;
}
.todo-desc{
    padding-top: 0.5rem;
    font-size: 1rem;
    color: #777;
}
