#del-icon{
    width: 1.25rem;
    margin: 0em 0.75em;
    filter: invert(25%) sepia(37%) saturate(7492%) hue-rotate(350deg) brightness(88%) contrast(135%);
    cursor: pointer;
}
#tick-icon{
    width: 1.5rem;
    margin: 0em 0.75em;
    filter: invert(33%) sepia(86%) saturate(4823%) hue-rotate(145deg) brightness(90%) contrast(80%);
    filter: invert(72%) sepia(16%) saturate(4772%) hue-rotate(11deg) brightness(113%) contrast(96%);
    filter: invert(66%) sepia(48%) saturate(5429%) hue-rotate(2deg) brightness(104%) contrast(106%);
    cursor: pointer;
}
.line-through{
    text-decoration: line-through  #777;
}
.tick-green{
    filter: invert(33%) sepia(86%) saturate(4823%) hue-rotate(145deg) brightness(90%) contrast(80%) !important;
}
@media only screen and (max-width: 600px){
    .todo-container{
        /* flex-direction: column; */
        width: 90%;

    }
    .todo-text-container{
        width: 85%;
    }
    .del-btn{
        margin: 1em 0;
    }
}
